import ajax from './ajax';
export const reqRegister = (obj) => ajax('/register', obj, 'POST');
export const reqLogin = (obj) => ajax('/login',obj,'POST');
export const reqAutoLogin = () => ajax('/autoLogin');
export const reqRecommend = () => ajax("/recommend");
export const reqNote = (obj) => ajax('/note',obj,'POST');
export const reqNotes = () => ajax('/getNotes');