import {combineReducers} from 'redux';

import {
  ACCOUNT_SUCCESS,
  ACCOUNT_ERROR,
  ACCOUNT_CLEAR,
  RECOMMEND_ERROR,
  RECOMMEND_SUCCESS,
  RECOMMEND_CLEAR,
  NOTE_ERROR,
  NOTE_SUCCESS,
  GET_NOTES_ERROR,
  GET_NOTES_SUCCESS
} from './action-types';

const initUser = {
  nickName:"",
  username:"",
  sex:"",
}

const initRecommend = [];

function user(state = initUser, action){
  switch(action.type){
    case ACCOUNT_SUCCESS : {
      return action.data
    }
    case ACCOUNT_ERROR : {
      return {...state, msg:action.data}
    }
    case ACCOUNT_CLEAR : {
      return initUser;
    }
    default :
      return state;
  }
}

function recommend(state = initRecommend, action){
  switch(action.type){
    case RECOMMEND_SUCCESS : {
      return action.data;
    }
    case RECOMMEND_ERROR : {
      return {msg:action.data}
    }
    case RECOMMEND_CLEAR : {
      return [];
    }
    default:
      return state;
  }
}

const initNote = {
  from_id:"",
  data:"",
  avatar_url:"",
  imgs_url:[],
  content:"",
  like:"",
  comment:[],
  share:""
}

function note(state = initNote, action){
  switch(action.type){
    case NOTE_SUCCESS : {
      return {state,...action.data}
    }
    case NOTE_ERROR : {
      return {state,msg:action.data}
    }
    default : {
      return state;
    }
  }
}

const initNotes = {
  imgs_url:"",
  comment:"",
  _id:"",
  from_id:"",
  date:"",
  content:"",
  like:"",
  share:"",
}

function notes(state = [], action){
  switch(action.type){
    case GET_NOTES_SUCCESS : {
      return action.data;
    }
    case GET_NOTES_ERROR : {
      return {msg:action.data}
    }
    default : {
      return state;
    }
  }
}

export default combineReducers({
  user,
  recommend,
  note,
  notes
});