import React, {Component} from 'react';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom';
import BScroll from 'better-scroll'
import {
  Form,
  Input,
  Tooltip,
  Icon,
  Button,
  Radio,
  message
} from 'antd';

import {
  register,
} from '../../redux/actions'

import './register.css'

class Register extends Component {
  state = {
    confirmDirty: false,
    autoCompleteResult: [],
  };

  //ËÆæÁΩÆraidoÈªòËÆ§ÂÄº
  componentDidMount(){
    this.props.form.setFieldsValue({
      sex: 'male',
    });
    const registerWrapper = document.querySelector('.register-wrapper');
     this.RegisterScroll = new BScroll(registerWrapper,{
      scrollY: true,
      click: true
  })
  }

  

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let result = this.props.register(values);
        result.then((value) => {
          if(this.props.user.username && !this.props.user.msg){
            message.success('Ê¨¢ËøéÂÖ•È©ªÔºåÂø´ÂéªÂØªÊâæ‰πêË∂£Âêßü¶Ñ');
            this.props.history.replace('/personal');
          }else if(this.props.user.msg){
            message.warning(this.props.user.msg);
            this.props.user.msg = '';
          }
        },(err) => {
          message.warning(this.props.user.msg);
          this.props.user.msg = '';
        })
      }
    });
  };

  handleConfirmBlur = e => {
    const { value } = e.target;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback('Two passwords that you enter is inconsistent!');
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  render() {

    const { getFieldDecorator } = this.props.form;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };

    return (
      <div className='register-wrapper'>
        <div className='register-container'>

      <Form {...formItemLayout} onSubmit={this.handleSubmit}>
        <Form.Item label='Username'>
          {getFieldDecorator('username', {
            rules: [
              {
                // type: 'text',
                message: 'The input is not valid E-mail!',
              },
              {
                required: true,
                message: 'Please input your E-mail!',
              },
            ],
          })(<Input />)}
        </Form.Item>
        <Form.Item label="Password" hasFeedback>
          {getFieldDecorator('password', {
            rules: [
              {
                required: true,
                message: 'Please input your password!',
              },
              {
                validator: this.validateToNextPassword,
              },
            ],
          })(<Input.Password />)}
        </Form.Item>
        <Form.Item label="Confirm Password" hasFeedback>
          {getFieldDecorator('confirm', {
            rules: [
              {
                required: true,
                message: 'Please confirm your password!',
              },
              {
                validator: this.compareToFirstPassword,
              },
            ],
          })(<Input.Password onBlur={this.handleConfirmBlur} />)}
        </Form.Item>

      <Form.Item
          label={
            <span>
              ÊÄßÂà´&nbsp;
              <Tooltip title="What do you want others to call you?">
                <Icon type="question-circle-o" />
              </Tooltip>
            </span>
          }
        >
      {getFieldDecorator('sex',{
                    rules: [
                      {
                        required: true,
                        message: 'Please confirm your password!',
                      },
                    ],
      })(
      <Radio.Group  buttonStyle="solid">
          <Radio.Button  value="male">Áî∑Áîü</Radio.Button>
          <Radio.Button value="female">Â•≥Áîü</Radio.Button>
        </Radio.Group>)}
        </Form.Item>

        <Form.Item
          label={
            <span>
              Nickname&nbsp;
              <Tooltip title="What do you want others to call you?">
                <Icon type="question-circle-o" />
              </Tooltip>
            </span>
          }
        >
          {getFieldDecorator('nickName', {
            rules: [{ required: true, message: 'Please input your nickname!', whitespace: true }],
          })(<Input />)}
        </Form.Item>

        <Form.Item {...tailFormItemLayout} >
          <div className='register-spec-btns'>
          <Button type="primary" htmlType="submit" >
            Register
          </Button>
          <Button type="primary" htmlType="submit" >
            ÂéªÁôªÂÖ•
          </Button>
          </div>
        </Form.Item>
      </Form>
      </div>
      </div>

    );
  }
}
const Register2 = Form.create({ name: 'register' })(Register);

export default connect(
  state => ({user:state.user}) ,
  {register}
)(Register2);